import React from 'react';
import AppLoading from 'expo-app-loading';
import { useFonts } from 'expo-font';
import { extendTheme, NativeBaseProvider, Text } from 'native-base';

const theme = extendTheme({
  fontConfig: {
    CCWildWords: {
      100: {
        normal: 'ccwildwords'
      }
    }
  },
  fonts: {
    heading: 'CCWildWords',
    body: 'CCWildWords',
    mono: 'CCWildWords'
  }
});

export const App = () => {

  let [fontsLoaded] = useFonts({
    'CCWildWords': require("./assets/fonts/ccwildwords.ttf")
  });

  if (!fontsLoaded) {
    return <AppLoading />;
  }

  return (
    <NativeBaseProvider theme={theme} >
      <Text fontFamily="body" fontSize="xl" fontStyle="normal" >Hello Doumo!</Text>
    </NativeBaseProvider>
  );
};

export default App;
